import { Component, ElementRef, Inject, OnInit, ViewChild } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { EsignPopComponent } from '../esign-pop/esign-pop.component';

@Component({
  selector: 'app-upload-sign-esign',
  templateUrl: './upload-sign-esign.component.html',
  styleUrls: ['./upload-sign-esign.component.scss']
})
export class UploadSignEsignComponent implements OnInit {
  isDone: boolean = false;
  show: any;
  // dialog: any;
  accountId: any;
  // apiService: any;
  accountdetailsForStatusUpdate: any;
  previousUrl: string = '';
  // accountService: any;
  customerId: any;

  imageURL1: any;
  urlService: any;
  selectedFile: any;
  res: Object = {'enable':false, 'image':null};
  image: string;
  profileId: any;
  @ViewChild("profileFile") profileFile: ElementRef;
  esignUrl: any;
  disableDone: boolean = true;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    private router: Router,
    private apiService: ApiService,
    private dialogService: DialogService,
    private dialog: MatDialog,
    public dialogRef: MatDialogRef<UploadSignEsignComponent>,
    private ls: LocalStoreService) { }

  ngOnInit(): void {
    this.image=this.ls.getItem('signedImage');
    if(this.image){
      this.imageURL1 =this.image;
    }else{
      this.imageURL1 = "not_available";
    }
    
    this.accountId = this.ls.getItem('accountId');

    // this.urlService.previousUrl$
    //   .subscribe((previousUrl: string) =>{
    //     console.log('resp-----',previousUrl);
        
    //     this.previousUrl = previousUrl
    //   });
  }

  close1()
  {
    if(this.data.screen === "NewCustomerOnboarding") {
      this.dialogRef.close(this.res);
    }
    else {
      this.dialogRef.close(true);
      this.ls.setItem('enable',true);
    }
    this.router.navigateByUrl('/', {skipLocationChange: true}).then(() => {
      this.router.navigate(['/home/application/digital-sign/options/',
      this.accountId]);
  });
  }

  openPopUp(data?: any) {
    let response:any;
    if(data === undefined ||data === null) {
      response = {
        isSignLater: false,
        screen: 'Create Account'
      };
    } 
     if(!this.show) {
      let dialogRef: MatDialogRef<any> = this.dialog.open(EsignPopComponent, {
        width: '720px',
        disableClose: true,
        data: ((data === undefined ||data === null) ? response : data)
      })
      dialogRef.afterClosed().subscribe(res => {
        console.log("dismiss res",res);
        if(res){
          this.imageURL1 = res?.image;
          this.disableDone = res?.image ? false : true;
          // this.esignUrl = res?.fileUrl;
          this.show= this.ls.setItem('enable', true);
          console.log(this.show);
          // this.isDone=true;
         
        }
      })
     }

}
 
  // goBack() {
  //   this.router.navigate([this.previousUrl]);
  // }

  onFileSelect(event) {
    console.log("event -- ", event.target.files);
    this.selectedFile = event.target.files[0]
    console.log(this.selectedFile)

    let reader = new FileReader();
    reader.readAsDataURL(event.target.files[0]);
    reader.onload = (event2) => {
      this.imageURL1 = reader.result;
    }; 

    const cId: string = this.customerId;
    const url: string = this.selectedFile;
    const uploadData = new FormData();
    const data = {
      customerId: this.customerId,
      documentName: "Signature",
      documentType: "10",
      fileType: event.type,
      fileName: event.name,
      verificationType: "Digital Signing"
    };
    uploadData.append("data", JSON.stringify(data));
    uploadData.append("file", url);
    
    this.apiService.upload(uploadData).subscribe((res) => {
      if(res){
       
      }
     });
    
  }
  onSubmit(){
    this.isDone=true;
    console.log(this.isDone);
    this.dialogRef.close(true);
    
  }
  deleteImg() {
    this.dialogService.doConfirmDialog('').subscribe((response) => {
      if (response == "Yes") {
        this.imageURL1 = "not_available";
        this.profileFile.nativeElement.value = '';
        this.apiService.deleteCustomerDoc(this.customerId, this.profileId).subscribe((data) => {
        });
        this.disableDone = true;
        this.ls.setItem('enable',true);
      }
    });

  }

}
